import 'uni-pages';import '@dcloudio/uni-stat';
import Vue from 'vue'
import App from './App'

import * as Api from './config/api.js'
import * as Common from './config/common.js'
import * as Db from './config/db.js'
import * as Config from './config/config.js'

//Vue.prototype.contextPath="nmgtyj/"
//Vue.prototype.baseurl="http://192.168.10.217:8080/nmgtyj/";
Vue.prototype.contextPath="/"
Vue.prototype.baseurl="http://localhost:8080/pages/api.json";
//用户登录信息			
Vue.prototype.webUser={};

Vue.prototype.$api = Api;
Vue.prototype.$common = Common;
Vue.prototype.$db = Db;
Vue.prototype.$config = Config;


Vue.config.productionTip = false

import cuCustom from './colorui/components/cu-custom.vue'
Vue.component('cu-custom',cuCustom)

import mainpage from './pages/main/index.vue'
Vue.component('mainpage',mainpage)

import warnpage from './pages/warn/index.vue'
Vue.component('warnpage',warnpage)

import noticepage from './pages/notice/index.vue'
Vue.component('noticepage',noticepage)

import settingpage from './pages/setting/index.vue'
Vue.component('settingpage',settingpage)

import messagepage from './pages/affair/index.vue'
Vue.component('messagepage',messagepage)


Vue.prototype.downloadQR = "/static/logo.png";
Vue.prototype.defaultimg = "data:image/png;base64,";
Vue.prototype.baseurl=Config.baseurl;
Vue.prototype.$webUser=function(){//当前登录账号
	const webUserstr = uni.getStorageSync('webUser');
	if(webUserstr!=""&&webUserstr!=null){
		var webuser=JSON.parse(webUserstr);
		if(!webuser.rolename)webuser.rolename="物业"
		//webuser.storeyid="40ece50bd5b743d7bf1dd176f3ac55b8_1_2_2";//楼层标识
		//webuser.roomid="9";
		return webuser;	 	
		
	}else{
		return null;
	}
}

App.mpType = 'app'

const app = new Vue({
    ...App
})
app.$mount()